.project-history {

  h2 {
    align-self: flex-start;
    margin-bottom: 3.5rem;

    @media (min-width: 1024px) {
      margin-bottom: 6rem;
    }
  }

  .blog-center__inner-tabs {
    margin-bottom: 2rem;

    @media (min-width: 1024px) {
      margin-bottom: 4rem;
    }

    .blog-center__inner-tab {

      @media (min-width: 650px) {
        width: auto;
        padding: 2.1rem 2.88rem;
      }
    }
  }

  &__content {
    // position: absolute;
    // top: 0;
    // left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 2rem;
    // margin-bottom: 21.3rem;

    @media (min-width: 1024px) {
      flex-direction: row;
      justify-content: space-between;
      // align-items: initial;
    }

    &-text {
      width: 100%;
      max-width: 55.9rem;

      p {
        margin-bottom: 2rem;
        // @include InterRegular();
        font-size: 1.6rem;
        line-height: 130.02%;
        color: var(--black_1);

        &:not(:last-of-type) {
          margin-bottom: 2rem;

          @media (min-width: 1024px) {
            margin-bottom: 3rem;
          }
        }
      }
    }

    img {
      width: 100%;
      max-width: 35.7rem;
      height: 25.4rem;
      order: -1;
      margin-bottom: 2rem;

      @media (min-width: 600px) {
        max-width: 49rem;
      }

      @media (min-width: 1024px) {
        order: 0;
        margin-bottom: 0;
      }
    }
  }

  // &__wrap {
  //   width: 100%;
  //   height: 100%;
  //   display: flex;
  //   justify-content: space-between;
  //   position: relative;
  //   margin-bottom: 50rem;
  // }
}

.tabs-history {
  display: flex;
  // flex-wrap: wrap;
  justify-content: space-around;
  width: 100% !important;

  .tab--big {
    width: 19rem;

    // @media (min-width: 375px) {
    //   width: 19rem;
    // }
  }
}